<template>
  <div class="home position-relative">
    <HeaderNavbar></HeaderNavbar>
    <NavBar class="col-2 p-0 position-fixed top-0 start-0" v-if="isDesktop"></NavBar>
    <main>
      <router-view/>
    </main>
    <FooterPart></FooterPart>
    <PanelModal v-if="isPhone"></PanelModal>
  </div>
</template>

<style>
a {
  text-decoration: none;
  color: white;
}
.col-2 {
  width: 240px;
  z-index: 101;
}
@media screen and (min-width: 1248px) {
  .home {
    padding-left: 240px;
  }
}
</style>

<script>
// @ is an alias to /src
import NavBar from '@/components/NavBar.vue'
import HeaderNavbar from '@/components/HeaderNavbar.vue'
import FooterPart from '@/components/FooterPart.vue'
import PanelModal from '@/components/PanelModal.vue'
export default {
  name: 'HomeView',
  data () {
    return {
      isDesktop: true,
      isPhone: false
    }
  },
  components: {
    NavBar,
    HeaderNavbar,
    FooterPart,
    PanelModal
  },
  methods: {
    checkWindowWidth () {
      this.isDesktop = window.innerWidth >= 1248
      this.isPhone = window.innerWidth <= 767
    }
  },
  mounted () {
    this.checkWindowWidth()
    window.addEventListener('resize', this.checkWindowWidth)
  },
  beforeUnmount () {
    window.removeEventListener('resize', this.checkWindowWidth)
  }
}
</script>
